@media (min-width: 640px) {
  .sm\:hidden {
    display: none;
  }

  .sm\:w-\[300px\] {
    width: 300px;
  }

  .sm\:w-\[80\%\] {
    width: 80%;
  }

  .sm\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .sm\:flex-row {
    flex-direction: row;
  }

  .sm\:pt-20 {
    padding-top: 5rem;
  }

  .sm\:text-xl {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
}

@media (min-width: 768px) {
  .md\:left-1\/2 {
